@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@400;500;600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  * {
    @apply border-primary-600;
  }
  
  body {
    @apply bg-primary-900 text-primary-50 font-body;
    font-feature-settings: "rlig" 1, "calt" 1;
  }

  h1, h2, h3, h4, h5, h6 {
    @apply font-display font-semibold;
  }

  h1 {
    @apply text-4xl md:text-5xl;
  }

  h2 {
    @apply text-3xl md:text-4xl;
  }

  h3 {
    @apply text-2xl md:text-3xl;
  }

  h4 {
    @apply text-xl md:text-2xl;
  }

  h5 {
    @apply text-lg md:text-xl;
  }

  h6 {
    @apply text-base md:text-lg;
  }

  input, textarea, select {
    @apply bg-primary-800 border-primary-600 text-primary-100 placeholder-primary-400;
  }

  input:focus, textarea:focus, select:focus {
    @apply border-accent-500 ring-accent-500/20;
  }
}

@layer components {
  .btn {
    @apply px-4 py-2 rounded-lg font-medium transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-primary-900;
  }

  .btn-primary {
    @apply btn bg-accent-600 text-white hover:bg-accent-700 focus:ring-accent-500;
  }

  .btn-secondary {
    @apply btn bg-primary-700 text-primary-100 hover:bg-primary-600 focus:ring-primary-500;
  }

  .btn-outline {
    @apply btn border-2 border-primary-600 text-primary-200 hover:bg-primary-700 focus:ring-primary-500;
  }

  .btn-danger {
    @apply btn bg-error-600 text-white hover:bg-error-700 focus:ring-error-500;
  }

  .btn-success {
    @apply btn bg-success-600 text-white hover:bg-success-700 focus:ring-success-500;
  }

  .btn-sm {
    @apply px-3 py-1.5 text-sm;
  }

  .btn-lg {
    @apply px-6 py-3 text-lg;
  }

  .btn-disabled {
    @apply opacity-50 cursor-not-allowed;
  }

  .input {
    @apply w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-offset-2 focus:ring-offset-primary-900 transition-all duration-200;
  }

  .input-error {
    @apply border-error-500 text-error-500 focus:ring-error-500;
  }

  .card {
    @apply bg-primary-800 rounded-lg shadow-tattoo border border-primary-700;
  }

  .card-body {
    @apply p-6;
  }

  .card-header {
    @apply px-6 py-4 border-b border-primary-700;
  }

  .card-footer {
    @apply px-6 py-4 border-t border-primary-700;
  }

  .badge {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
  }

  .badge-primary {
    @apply badge bg-accent-100 text-accent-800;
  }

  .badge-success {
    @apply badge bg-success-100 text-success-800;
  }

  .badge-warning {
    @apply badge bg-warning-100 text-warning-800;
  }

  .badge-error {
    @apply badge bg-error-100 text-error-800;
  }

  .modal-overlay {
    @apply fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4;
  }

  .modal-content {
    @apply bg-primary-800 rounded-lg shadow-tattoo-lg max-w-md w-full max-h-[90vh] overflow-auto;
  }

  .loading-spinner {
    @apply animate-spin rounded-full h-8 w-8 border-b-2 border-accent-600;
  }

  .nav-link {
    @apply px-3 py-2 rounded-md text-sm font-medium transition-colors duration-200;
  }

  .nav-link-active {
    @apply nav-link bg-accent-600 text-white;
  }

  .nav-link-inactive {
    @apply nav-link text-primary-300 hover:bg-primary-700 hover:text-primary-100;
  }

  .form-group {
    @apply space-y-2;
  }

  .form-label {
    @apply block text-sm font-medium text-primary-200;
  }

  .form-error {
    @apply text-sm text-error-500 mt-1;
  }

  .form-help {
    @apply text-sm text-primary-400 mt-1;
  }
}

@layer utilities {
  .text-balance {
    text-wrap: balance;
  }

  .scrollbar-hide {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }

  .scrollbar-hide::-webkit-scrollbar {
    display: none;
  }

  .gradient-mask {
    mask-image: linear-gradient(to bottom, black 0%, black 80%, transparent 100%);
  }
}

/* Dark mode specific overrides */
@media (prefers-color-scheme: dark) {
  body {
    @apply bg-primary-50 text-primary-900;
  }
}

/* Print styles */
@media print {
  body {
    @apply text-black bg-white;
  }
  
  .no-print {
    display: none !important;
  }
}